.banner {
    padding: 20px 0 96px;
    &__inner {
        &-left {
            width: 33.33%;
        }
        &-right {
            display: flex;
            width: 66.66%;
        }
        .flexCell {
            margin-top: 30px;
            width: 100%;
        }
    }
    &__item {
        display: flex;
        text-align: center;
        .item__inner {
            flex-direction: row;
            align-items: center;
            justify-content: center;
            padding: 69px 77px 59px;
            &:hover {
                .item__right {
                    width: 0;
                    margin: 0;
                    overflow: hidden;
                }
                .item__img {
                    background-position: center;
                    transform: scale(1.2);
                }
            }
        }
        .item__img {
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            height: 242px;
            transition: .5s;
        }
        .title-h3, .title-h1 {
            text-transform: none;
            margin: 0;
        }
        &-reverse {
            .item__inner {
                flex-direction: row-reverse;
            }
            .item__right {
                margin: 0 15px 0 0;
            }
        }
        &-big {
            .item__left {
                width: 43vw;
            }
            .item__right {
                margin: 0 0 0 123px;
            }
            .item__img {
                height: 393px;
            }
            .title-h1 {
                span {
                    display: block;
                    margin-bottom: 5px;
                    font-size: 30px;
                    font-weight: 400;
                }
            }
        }
    }
}
@media(max-width: 1450px) {
    .banner {
        &__inner {
            &-left {
                width: 44%;
            }
            &-right {
                width: 56%;
            }
        }
        &__item {
            .item__inner {
                padding: 55px;
            }
            &-big {
                .item__right {
                    margin: 0 0 0 50px;
                }
                .title-h1 {
                    span {
                        font-size: 24px;
                    }
                }
            }
        }
    }
}
@media(max-width: 991px) {
    .banner {
        &__inner {
            flex-wrap: wrap;
            &-left, &-right {
                width: 100%;
            }
        }
        &__item {
            .item__inner {
                padding: 65px;
            }
            .item__right {
                margin: 0 0 0 20px;
            }
            .item__left {
                width: 43vw;
            }
            .item__img {
                background-position: 0 center;
            }
            &-reverse {
                .item__right {
                    margin: 0 20px 0 0;
                }
                .item__img {
                    background-position: 100% center;
                }
            }
            &-big {
                .item__img {
                    height: 242px;
                }
                .title-h1 {
                    font-family: var(--mainFont);
                    font-size: 22px;
                    span {
                        font-size: 22px;
                        margin: 0;
                    }
                }
            }
            .title-h3 {
                text-transform: uppercase;
                span {
                    text-transform: none;
                }
            }
        }
    }
}
@media(max-width: 900px) {
    .banner {
        padding: 20px 0 60px;
    }
}
@media(max-width: 556px) {
    .banner {
        &__item {
            .item__inner {
                flex-direction: column;
                padding: 120px 30px 25px;
                &:hover {
                    .item__right {
                        width: auto;
                        opacity: 0;
                    }
                }
            }
            .item__img {
                background-position: center;
            }
            .title-h3, .title-h1 {
                margin-top: 22px;
            }
            .item__right {
                margin: 0;
            }
            .item__left {
                width: 100%;
            }
        }
    }
}